import createGraph, { Graph } from "ngraph.graph";
import { LatLng } from "leaflet";
import MapData from "../models/MapData";

export interface NodeData {
    position: LatLng;
    clickRadius: number;
    encounterId?: string;
    exitId?: string;
}

export interface VertexData {
    weight: number;
}

/*
  Convert the Map data DTO to an ngaph.graph object for pathfinding
*/
export default class GraphBuilder {
    public static Build(mapData: MapData): Graph<NodeData, VertexData> {
        let outGraph = createGraph<NodeData, VertexData>();

        for (let i = 0; i < mapData.vertices.length; i++) {
            const item = mapData.vertices[i];
            outGraph.addNode(item.id, {
                position: item.position,
                clickRadius: item.clickRadius,
                encounterId: item.encounterId,
                exitId: item.exitId,
            });
        }

        for (let i = 0; i < mapData.edges.length; i++) {
            const item = mapData.edges[i];
            outGraph.addLink(item.to, item.from, { weight: item.weight });
        }

        return outGraph;
    }
}
