import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles({
    appHeader: {
        backgroundColor: "#282c34",
        minHeight: "100px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        fontSize: "calc(10px + 2vmin)",
        color: "white",
    },
    headerSpan: {
        width: "100%",
        display: "flex",
    },
    headerParty: {
        position: "absolute",
        zIndex: 1000,
        right: "10px",
        top: "30px",
    },
    headerImg: {
        width: "90px",
        height: "90px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "#282c34",
        margin: "4px",
    },
    appLogo: {
        height: "70px",
        pointerEvents: "none",
        padding: "20px",
    },
});

type HeaderProps = {};

function Header(props: HeaderProps) {
    const classes = useStyles();

    return (
        <header className={classes.appHeader}>
            <span className={classes.headerSpan}>
                <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                <img className="title shadow" src="title.svg" />
                <span className={classes.headerParty}>
                    <a href="/#/profile/1">
                        <img src="images/knight.jpg" className={classes.headerImg} alt="profile" />
                    </a>
                    <a href="/#/profile/2">
                        <img src="images/cleric.jpg" className={classes.headerImg} alt="profile" />
                    </a>
                    <a href="/#/profile/3">
                        <img src="images/rogue.jpg" className={classes.headerImg} alt="profile" />
                    </a>
                    <a href="/#/profile/4">
                        <img src="images/wizard.jpg" className={classes.headerImg} alt="profile" />
                    </a>
                </span>
            </span>
        </header>
    );
}

export default Header;
