import "./App.scss";
import { HashRouter, Route, Switch } from "react-router-dom";

import Header from "./components/Header";
import OverheadMap from "./pages/OverheadMap";
import HeroProfile from "./pages/HeroProfile";
import EncounterMap from "./pages/EncounterMap";

function App() {

    return (
        <div className="app">
            <Header />
            <HashRouter>
                <Switch>
                    <Route
                        path="/profile/:id"
                        component={() => {
                            return <HeroProfile />;
                        }}
                    />
                    <Route path="/encounter" component={EncounterMap} />
                    <Route path="/:id" component={OverheadMap} />
                    <Route path="/" component={OverheadMap} />
                </Switch>
            </HashRouter>
        </div>
    );
}

export default App;
